<template>
  <swiper
    :modules="modules"
    :slides-per-view="1"
    :speed="500"
    :loop="true"
    :space-between="0"
    :autoplay="{ delay: 3000, disableOnInteraction: false }"
    effect="fade"
    :pagination="paginationOpt"
    @swiper="onSwiper"
    @slideChange="onSlideChange"
  >
    <swiper-slide v-for="item in images" :key="item.id">
      <img class="swiper-img" :src="item.img_url" alt="" />
    </swiper-slide>
  </swiper>
</template>

<script setup>
/**
 * :
 * speed: sliderms
 * loop: 
 * space-between: 
 * autoplay: 
 * effect: 
 * autoplaydelay: ms
 * autoplaydisableOnInteraction: swiperautoplaytrue
    falseswiperautoplay
    (touch)pagination
  pagination: 
 */

// : https://juejin.cn/post/7091796748624101384     https://swiperjs.com/vue
import { reactive } from 'vue'
import { Swiper, SwiperSlide } from 'swiper/vue'
//  
// import { Autoplay, EffectFade } from "swiper";
import { Autoplay, Pagination } from 'swiper'
// 
import 'swiper/css'
import 'swiper/css/autoplay'
import 'swiper/css/effect-fade'
import 'swiper/scss/pagination'

// const modules = [Autoplay, EffectFade]
const modules = [Autoplay, Pagination]

const onSwiper = (swiper) => {
  // console.log(swiper);
}

// 
const onSlideChange = () => {}

// https://www.swiper.com.cn/api/pagination/299.html
const paginationOpt = reactive({
  clickable: true,
  /**
   * :
      bullets  
      fraction   ()
      progressbar  
      custom 
           */
  type: 'fraction'
})

defineProps({
  images: {
    type: Array
  }
})
</script>

<style lang="scss" scoped>
.swiper:deep() {
  .swiper-slide {
    height: 414px;
    line-height: 414px;
    text-align: center;
  }
  .swiper-pagination {
    // 
    left: 85%;
    width: 45px;
    z-index: 2;
    background: rgba(0, 0, 0, 0.3);
    color: #fff;
    padding: 2px;
    overflow: hidden;
    border-radius: 2px;
    font-size: 10px;
    span {
      vertical-align: baseline;
    }

    // bullets()
    .swiper-pagination-bullet {
      background: #ffffff;
    }
    .swiper-pagination-bullet-active {
      background: $primary;
    }
  }
}

.swiper-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>
